.list {
    display: flex;
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
    list-style-type: none;
    padding-left: 0;
}

.percentage_section {
    position: relative;

    display: flex;
    align-items: center;
    justify-content: center;
    height: 2rem;
    padding: 0 0.4rem;

    font-size: 1rem;
    text-align: center;
    cursor: pointer;

    box-shadow: 0 0 5px rgba(33, 33, 33, 0.7);
    transition: all .3s ease-in;
}

.percentage_section:hover > .tooltip_span {
    opacity: 1;
    transform: translate(-50%, 0);
}

.tooltip_span {
    position: absolute;
    left: 50%;
    bottom: 100%;
    opacity: 0;
    margin-bottom: 0.4rem;
    padding: 0.4rem;
    background-color: #333;
    color: #e2e2e2;
    font-size: 1.2rem;
    line-height: 1.6;
    text-align: center;
    white-space: nowrap;
    transform: translate(-50%, 0.4rem);
    transition: all .15s ease-in-out;
}

.tooltip_span::before {
    content: '';
    position: absolute;
    top: 100%;
    left: 50%;
    width: 0;
    height: 0;
    border: .5em solid transparent;
    border-top-color: #000;
    transform: translate(-50%, 0);
}

.text_span {
    font-weight: bold;
    font-size: 1.4rem;
}

.percentage_checkbox {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 1em;
    width: 18px;
    height: 18px;
    box-sizing: border-box;
    background: transparent;
    border: 2px solid #9E9E9E;
    border-radius: 2px;
    cursor: pointer;  
    transition: all 250ms cubic-bezier(.4,.0,.23,1);
}

.fake_checkbox {
    display: none;
}

.fake_checkbox:checked + label > .percentage_checkbox {
    border-width: 9px;
    border-style: solid;
    border-color: inherit;
    animation: shrink-bounce 200ms cubic-bezier(.4,.0,.23,1);
}

@keyframes shrink-bounce {
    0%{
      transform: scale(1);
    }
    33%{    
      transform: scale(.85);
    }
    100%{
      transform: scale(1);    
    }
}