:root {
    --main-square-primary-color: rgb(106, 114, 131);
    --main-square-secondary-color: rgb(41, 48, 61);
    --main-square-highlight-primary-color: rgb(247, 245, 125);
    --main-square-highlight-secondary-color:  rgb(186, 202, 73);
    --board-piece-size: 32px;
    --board-bank-piece-size: 32px;
    --ghostboard-square-color: #333;
    --sidebar-background-color: rgba(39, 38, 35, 0.5);
}

.board-wrap {
    position: relative;
    width: min-content;
}

.board-piece-bank-container {
    width: 0;
    height: 0;
}

.board-piece-bank-wrap {
    position: absolute;
    overflow: hidden;
    border-radius: 3px;
    cursor: pointer;
}

.board-piece-bank-wrap:hover {
    border-color: hsla(0, 0%, 100%, 0.6);
}

.board-piece-bank-wrap--right {
    left: 100%;
}

.board-piece-bank-wrap--left.board-piece-bank {
    direction: rtl;
}

.board-piece-bank-wrap--left {
    right: 100%;
}

.board-piece-bank-wrap--bottom {
    bottom: 0;
}

.board-piece-bank-wrap--top {
    top: 0;
}

.board-piece-bank {
    z-index: unset;
    display: grid;
    grid-auto-flow: column;
    grid-template-rows: repeat(5, 1fr);
    background-color: var(--sidebar-background-color);
    font-weight: 600;
    user-select: none;
}

.board-piece-bank__piece-wrap {
    position: relative;
    width: var(--board-bank-piece-size);
    height: var(--board-bank-piece-size);
    border-radius: 5px;
}

.board-piece-bank__piece {
    position: relative;
    top: 0;
    left: 0;
    z-index: unset;
    width: var(--board-bank-piece-size);
    height: var(--board-bank-piece-size);
    transform: scale(78%);
    transition: transform 1s;
}

.board-piece-bank__piece-wrap:hover > .board-piece-bank__piece {
    transform: scale(100%);
}

.board-piece-bank__piece-count {
    position: absolute;
    right: 2%;
    bottom: 2%;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 45%;
    height: 45%;
    background-color: var(--sidebar-background-color);
    border-radius: 100px;
    color: #fff;
}

.board-dialog-window {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    pointer-events: none;
}

.board-dialog-window > * {
    pointer-events: all;
}

.board-promotion-dialog {
    display: flex;
    flex-wrap: wrap;
    background-color: rgba(0, 0, 0, 0.8);
    border-radius: 9px;
    cursor: pointer;
}

.board-promotion-dialog__piece-display {
    width: var(--board-piece-size);
    height: var(--board-piece-size);
}

.board-chained-move-notice {
    position: absolute;
    bottom: 3px;
    display: flex;
    align-items: center;
    padding: 2px 8px;
    border-radius: 3px;
    font-size: 17px;
    font-weight: 600;
    cursor: pointer;
}

.board-chained-move-notice--seirawan-drop {
    background: rgba(44, 44, 44, 0.66);
}

@keyframes backgroundBlink {
    50% {
        background-color: transparent;
    }
}

.board-chained-move-notice--blink {
    animation: 1s backgroundBlink;
    animation-timing-function: steps(1);
}

.board-chained-move-notice__description {
    font-weight: 500;
    color: #fff;
}

.board-chained-move-notice__countdown {
    font-weight: 500;
    color: #fff;
    margin-left: 8px;
}

.board-chained-move-notice__arrow {
    width: 20px;
    height: 15px;
    margin-left: 8px;
}

.game-row {
    display: flex;
    max-width: fit-content;
    padding-left: 0;
    list-style-type: none;
}

.game-row:nth-child(odd) > .game-square-wrap:nth-child(odd),
.game-row:nth-child(even) > .game-square-wrap:nth-child(even) {
    background-color: var(--main-square-primary-color);
}

.game-row:nth-child(odd) > .game-square-wrap:nth-child(even),
.game-row:nth-child(even) > .game-square-wrap:nth-child(odd) {
    background-color: var(--main-square-secondary-color);
}

.game-square-wrap {
    display: flex;
    width: var(--board-piece-size);
    height: var(--board-piece-size);
    justify-content: center;
    align-items: center;
}

.game-square {
    display: flex;
    width: inherit;
    height: inherit;
    justify-content: center;
    align-items: center;
}

.game-row:nth-child(odd) > .game-square-wrap:nth-child(odd) .game-square.game-square--active,
.game-row:nth-child(even) > .game-square-wrap:nth-child(even) .game-square.game-square--active {
    background-color: var(--main-square-highlight-primary-color);
}

.game-row:nth-child(odd) > .game-square-wrap:nth-child(even) .game-square.game-square--active,
.game-row:nth-child(even) > .game-square-wrap:nth-child(odd) .game-square.game-square--active {
    background-color: var(--main-square-highlight-secondary-color);
}

.game-row > .game-square-wrap .game-square.game-square--ghosted {
    background-color: var(--ghostboard-square-color);
}

.game-row > .game-square-wrap .game-square.game-square--fogged {
    background-color: inherit;
    filter: brightness(50%);
}

.game-square__piece {
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.game-player-box-container {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
}

.game-player-box-container.game-player-box-container--edge-extruded {
    top: 50%;
    left: 50%;
    width: 133%;
    height: 133%;
    transform: translate(-50%, -50%);
}

.game-player-box {
    display: flex;
    position: absolute;
}

.game-player-box--right {
    left: 100%;
}

.game-player-box--left {
    right: 100%;
}

.game-player-box--bottom {
    bottom: 0;
}

.game-player-box--top {
    top: 0;
}

.game-player-box__clock {
    color: #fff;
}

.game-player-box__clock-icon {
    width: 1.8rem;
    height: 1.8rem;
    padding: 0 0.2rem;
    font-size: 1.8rem;
    font-family: "Chess V3";
}

.game-player-box__clock-icon::before {
    content: "\0027";
}

.game-player-box__clock-seconds {
    font-family: "Chess New";
    font-size: 2rem;
}

.game-player-box__player-data {
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 0.4rem;
    justify-content: flex-start;
}

.game-player-box--left .game-player-box__player-data {
    flex-direction: row-reverse;
}

.game-player-box__player-icon {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 0.35rem;
}

.game-player-box-container--active .game-player-box__player-icon {
    filter: brightness(100%);
}

.game-player-box__player-info {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    color: #fff;
    font-size: 1.4rem;
    font-weight: 600;
    line-height: 1;
}

.game-player-box__player-points {
    position: absolute;
    height: 3.5rem;
    padding: 0 0.4rem;
    font-size: 3.5rem;
    font-weight: 500;
    line-height: 3.5rem;
    color: #fff;
    border-radius: 0.2rem;
    text-shadow: 0 0 0.5rem #fff;
}

.game-player-box--bottom .game-player-box__player-points {
    top: calc(100% + 0.45rem);
}

.game-player-box--top .game-player-box__player-points {
    bottom: calc(100% + 0.45rem);
}
